@import '../base';

.c-bundle-slider {
  @include t-v-gutters;

  &__pre-title,
  &__title {
    text-align: center;
    width: 100%;
    color: $s-color--font-1;
    max-width: t-to-rem(295);
    margin-left: auto;
    margin-right: auto;
  }

  &__pre-title {
    margin-bottom: t-to-rem(8);
  }

  &__title {
    margin-bottom: t-to-rem(42);
  }

  &__bundles {
    display: flex;
    padding-top: t-to-rem(48);


    &:not(.keen-slider) {
      justify-content: center;
      gap: 4%;
    }
  }

  &__bundle {
    width: t-to-rem(295);
    padding: t-to-rem(32) t-to-rem(8);

    &-variant {
      &-container {
        display: flex;
        align-items: baseline;
      }

      &-count {
        color: $s-color--font-6;
        background-color: $s-color--background-secondary;
      }

      &-factor {
        margin-left: 6px;
        margin-right: 2px;
        display: inline-block;
        align-self: baseline;
        line-height: 1;
        transform: rotate(45deg);
      }
    }

    &-hover-background-container {
      display: none;
    }

    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    text-align: center;
    z-index: 0;
    position: relative;
    flex-direction: column;
    justify-content: space-between;

    margin: 0 auto;

    border: 1px solid rgba($s-color--background-secondary, 0.2);
    background-color: $s-color--background-tertiary;

    transform: scale(1);
    transition: all $s-anim--time-slow $s-anim--ease-out;
    cursor: pointer;

    .keen-slider__slide.is-active &,
    .is-middle-bundle:not(.keen-slider__slide) & {
      margin-top: t-to-rem(-40);
      position: relative;
      background-color: $s-color--background;
      box-shadow: 0 t-to-rem(2) t-to-rem(4) t-to-rem(0) rgba($s-color--black, 0.25);
      transform: scale(1.02);

      &-title {
        color: $s-color--font-1;
      }

      &-variant {
        &-container {
          display: flex;
          align-items: center;
        }

        &-count {
          color: $s-color--font-6;
          background-color: $s-color--background-secondary;
        }

        &-factor {
          margin-left: 6px;
          margin-right: 6px;
        }
      }
    }

    &-variants {
      display: flex;
      justify-content: center;
      width: 100%;
    }

    &-variant {
      display: flex;
      flex-direction: row;
      align-items: center;
      justify-content: center;
      margin-right: t-to-rem(14);

      &:last-of-type {
        margin-right: unset;
      }

      &-count {
        color: $s-color--font-1;
        font-size: t-to-rem(16);
        background-color: $s-color--background;
        border-radius: 100%;
        margin-bottom: 0;
        display: flex;
        justify-content: center;
        align-items: center;
        @include t-width-and-height(22);
      }

      &-title {
        @include t-font--type-5;
        color: $s-color--font-1;
        font-size: t-to-rem(12);
      }
    }

    &-wrapper {
      position: relative;
      overflow: visible;
      flex-shrink: 0;
    }

    &-title {
      color: $s-color--font-6;
      margin-bottom: t-to-rem(16);
      font-size: t-to-rem(14);
    }

    &-image {
      height: 100%;
      width: auto;
      transform: scale(0.98);
      position: absolute;
      width: auto;
      transition: transform 1s;

      &-container {
        margin: t-to-rem(16) auto t-to-rem(26);
        position: relative;
        padding-bottom: 79%;
        width: 100%;
        display: flex;
        justify-content: center;
      }
    }

    &-cta {
      margin-top: auto;
      @extend %s-btn--primary;
      margin-bottom: t-to-rem(26);
      min-width: t-to-rem(112);
    }

    &-text {
      color: $s-color--font-1;
    }
  }

  &__add-cart {
    margin: 0 auto;

    &.o-btn {
      font-size: t-to-rem(12);
      padding-left: t-to-rem(16);
      padding-right: t-to-rem(16);
      text-align: center;
    }

    .is-divider {
      margin: 0 t-to-rem(4);
    }

    &-price {
      font-size: inherit;
      display: inline;
      color: $s-color--font-6;

      &-compare {
        text-decoration: line-through;
        color: rgba($s-color--font-6, .5);
        margin-right: t-to-rem(8);
      }
    }
  }

  &__dots {
    display: flex;
    justify-content: center;
    margin-top: t-to-rem(10);
  }

  &__dot {
    @include t-width-and-height(9);
    margin-right: t-to-rem(24);
    cursor: pointer;
    background-color: $s-color--font-1;
    opacity: 0.2;
    border-radius: 100%;

    &.is-active {
      opacity: 1;
    }

    &:last-of-type {
      margin-right: 0;
    }
  }

  /*==== Media Queries ====*/
  @include t-media-query($s-small-up) {

    &__pre-title,
    &__title {
      max-width: 45%;
    }

    &__bundle {
      width: t-to-rem(350);

      &-hover-background-container {
        display: block;
        opacity: 0;
        @include t-absolute-center-x-y;
        top: 44%;
        width: 60.9%;
        z-index: -1;
        display: flex;
        transition: opacity $s-anim--time-med;
      }

      &:hover {
        .c-bundle-slider__bundle {
          &-hover-background-container {
            opacity: 1;
          }

          &-image {
            transform: scale(1);
          }
        }
      }
    }

    .keen-slider__slide.is-active &,
    .is-middle-bundle:not(.keen-slider__slide) & {

      .c-bundle-slider__bundle-hover-background-container {
        svg path {
          stroke: $s-color--font-2;
        }
      }
    }
  }

  @include t-media-query($s-medium-up) {
    &__pre-title {
      margin-bottom: t-to-rem(20);
    }

    &__title {
      margin-bottom: t-to-rem(80);
    }

    &__dots {
      display: none;
    }

    &__bundles {
      justify-content: center;
    }

    &__bundle {
      padding: t-to-rem(32) t-to-rem(8);

      &-title {
        font-size: t-to-rem(18);
      }

      &-variant-title {
        font-size: t-to-rem(12);
      }
    }

    &__add-cart {
      &.o-btn {
        font-size: 1rem;
      }
    }
  }

  @include t-media-query($s-large-up) {
    &__title {
      margin-bottom: t-to-rem(95);
    }

    &__bundle {
      padding: 12% t-to-rem(15);

      .keen-slider__slide.is-active &,
      .is-middle-product:not(.keen-slider__slide) & {
        margin-top: t-to-rem(-50);
      }

      &-title {
        font-size: t-to-rem(24);
        margin-bottom: 7%;
      }

      &-variants {
        margin-bottom: t-to-rem(26);
      }

      &-variant {
        &-title {
          font-size: t-to-rem(16);
        }
        &-factor {
          align-self: center;
        }
      }
    }

    &__add-cart {
      &.o-btn {
        min-width: t-to-rem(200);
        width: 100%;
        font-size: t-to-rem(20);
        padding: t-to-rem(13) t-to-rem(15);
        margin-bottom: t-to-rem(22);
      }

      .is-divider {
        margin: 0 t-to-rem(6);
      }
    }
  }

  @include t-media-query($s-xlarge-up) {
    &__pre-title {
      margin-bottom: t-to-rem(22);
    }

    &__title {
      margin-bottom: t-to-rem(90);
    }

    &__bundles {
      &:not(.keen-slider) {
        justify-content: center;
        gap: 5%;
      }
    }

    &__bundle {
      width: t-to-rem(450);

      &-title {
        font-size: t-to-rem(30);
      }

      &-variant {
        flex-direction: row;
        margin-right: t-to-rem(18);

        &-count {
          @include t-width-and-height(30);
          margin-right: t-to-rem(8);
          margin-bottom: unset; //trump
        }

        &:last-of-type {
          margin-right: unset;
        }
      }
    }

    &__add-cart {
      &.o-btn {
        font-size: t-to-rem(22);
      }
    }
  }

  @include t-media-query($s-xxlarge-up) {
    &__bundle {
      &-title {
        font-size: t-to-rem(36);
      }

      width: t-to-rem(508);

      &-variant-title {
        font-size: t-to-rem(18);
      }
    }
  }
}
